import Item from './Item.vue'
import '../vue-tree.css'

export default {
  components: { 'tree-item': Item },

  model: {
    prop: 'ids',
    event: 'change'
  },

  props: {
    treeData: {
      type: Array,
      default: function () {
        return []
      }
    },
    options: {
      type: Object,
      default: function () {
        return {}
      }
    },
    ids: {
      type: Array,
      default: function () {
        return []
      }
    }
  },

  data () {
    return {
      defaultOptions: {
        label: 'label',
        checkbox: true,
        checkedOpen: true,
        folderBold: true,
        idsWithParent: true,
        addClass:'fa fa-plus text-danger',
        depthOpen: 0,
        openIcon: 'fa fa-angle-right',
        closeIcon: 'fa fa-angle-down',
        halfCheckedIcon: 'fa fa-minus-square-o fa-fw',
        checkedIcon: 'fa fa-check-square-o fa-fw',
        uncheckedIcon: 'fa fa-square-o fa-fw',
        showDelete:false,
        addItem:false,
        showEdit: true,
        editClass: 'fa fa-edit',
        deleteClass: 'fa fa-trash-o',
      },
      termOptions: {},
      idsWithParent: [],
      state: 0
    }
  },

  created () {
    this.initOptions()
    this.idsWithParent = this.ids.slice(0)
  },

  watch: {
    options: {
      handler: function (val) {
        this.initOptions()
      },
      deep: true
    }
  },

  methods: {
    handle (item) {
      this.$emit('handle', item)
    },
    initOptions () {
      this.termOptions = Object.assign({}, this.defaultOptions, this.options)
      this.idsWithParent = this.ids.slice(0)
    },
    addItem(obj){
      this.$emit('OnAdd',obj)
    },
    delItem(obj){
      this.$emit('OnDelete',obj)
    },
    editItem(obj){
      this.$emit('OnEdit',obj)
    }
  }
}



